<script>
import HeaderComponent from "@/components/Header1/header1.vue";
export default {
  name: "ticketInformationPrintComponent",
  components: { HeaderComponent },
  data() {
    return {
      data: {},
    };
  },
  computed: {},
  methods: {
    formatDate(data) {
      this.data.violationDate =
        data.violation_Date.split("T")[0].split("-").join("/") +
        " " +
        data.violation_Date.split("T")[1];
      this.data.violationTime =
        data.violation_Time.split("T")[0].split("-").join("/") +
        " " +
        data.violation_Time.split("T")[1];
      this.data.endTime =
        data.end_Time.split("T")[0].split("-").join("/") +
        " " +
        data.end_Time.split("T")[1];
    },
    getFineInfo(fineId) {
      this.$store
        .dispatch("QryFineInfo", fineId)
        .then((res) => {
          const data = res.data.data;
          this.data = data;
          this.formatDate(this.data);
        })
        .catch((err) => console.log(err));
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
    this.$root.overlay = false;
    const fine_Id = this.$route.params.fine_Id;
    this.getFineInfo(fine_Id);
  },
};
</script>

<template src="./ticketInformationPrint.html"></template>
